*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.box{
     display: flex;
     flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
   
}
.box h2{
    margin: 1rem;
    color: white;
}
.container{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 20rem;
    width: 30rem;
    margin-top: 10rem;
    margin-left: 1rem;
    margin-right: 1rem;

    border-radius: 1rem;
    box-shadow:  4px 6px 0px 0px rgb(229, 175, 148) ;
    background: #E71989;
    color: white;
}
.slideUpBtn {
    padding: 12px 24px;
    color: #000000;
    font-weight: bold;
    background-color: #8ecae6;
    font-family: 'RocknRoll One', sans-serif;
    border: 2px solid hsl(243, 80%, 62%);
    border-radius: 6px;
    position: relative;
    overflow: hidden;
    transition: all 0.5s cubic-bezier(1,.15,.34,.92);
    cursor: pointer;
    outline: none;
}

.slideUpBtn span {
    display: inline-block;
    transition: inherit;
    color: hsl(243, 80%, 62%);
}

.slideUpBtn:hover span {
    opacity: 0;
    transform: translateY(-100%)
}

.slideUpBtn::before {
    content: "";
    background-color: hsl(243, 80%, 62%);;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    transform: translateY(100%);
    transition: inherit;
    width: 100%;
    transition: transform 0.5s cubic-bezier(1,.15,.34,.92)
}

.slideUpBtn::after {
    content: 'New Joke';
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: center;
    left: 0;
    position: absolute;
    top: 0;
    transition: inherit;
    transform: translateY(100%);
    width: 100%;

}

.slideUpBtn:hover::before {
    transform: translateY(0) scale(3);
    transition-delay: .025s;
}

.slideUpBtn:hover::after {
    opacity: 1;
    color: hsl(222, 100%, 95%);
    transform: translateY(0);
}

.joke{
    padding: 1rem;
    font-family: 'Akaya Telivigala', cursive;
    color: white;
    font-size: 1.3rem;
}

@media (max-width: 400px){
    .slideUpBtn{
        background-color: hsl(243, 80%, 62%);
        color:#fff;
    }
    .container{
        margin-top: 8rem;
        width: 20rem;
        heigth: 30rem;
    }
}